<template>
<div>
    <div @click="soundPlay" class="game" ref="game" >
          <input type="button" class="panel-green" ref="green"
            :class="{'panel-green-click': this.panelClick.currentPanel[this.panelClick.currentPanel.length-1] == 0}">

          <input type="button" class="panel-red" ref="red"
            :class="{'panel-red-click': this.panelClick.currentPanel[this.panelClick.currentPanel.length-1] == 1}">

          <input type="button" class="panel-yellow" ref="yellow"
            :class="{'panel-yellow-click': this.panelClick.currentPanel[this.panelClick.currentPanel.length-1] == 2}">

          <input type="button" class="panel-blue" ref="blue"
            :class="{'panel-blue-click': this.panelClick.currentPanel[this.panelClick.currentPanel.length-1] == 3}">

          <div class="bar">
            <span class="bar_score" ref="score">Score: 0</span>
            <input @click="gameStart" type="button" class="bar_start" value="Start" ref="startBtn">
            <div class="bar_diff">
              <input class="bar_diff-dot dot1" type="radio" name="dot">
              <input class="bar_diff-dot dot2" type="radio" name="dot">
              <input class="bar_diff-dot dot3" type="radio" name="dot">
            </div>
          </div>
    </div>
</div>
</template>

<script>
import {Howl} from "howler"

export default {
  data() {
    return {
      panelClick: {
        currentPanel: [],
        allBot: [],
        player: []
      },
      clickable: false,
      panelsList: [0,1,2,3],
      score: 1,
      diff: 0,
      sounds: [
        new Howl({src: ["https://s3.amazonaws.com/freecodecamp/simonSound1.mp3"]}),
        new Howl({src: ["https://s3.amazonaws.com/freecodecamp/simonSound2.mp3"]}),
        new Howl({src: ["https://s3.amazonaws.com/freecodecamp/simonSound3.mp3"]}),
        new Howl({src: ["https://s3.amazonaws.com/freecodecamp/simonSound4.mp3"]})
      ]
    }
  },
  methods: {
    soundPlay(event) {
      if (!this.clickable) {
        return 
      }
      switch(event.target.className) {
        case "panel-green": 
        //  this.panelClick.currentPanel.push(0);
          this.playerClick(0)
         // this.panelClick.playerClick.push(0)
         // this.sounds[0].play(); 
          break;

        case "panel-red":
          this.playerClick(1) 
          break;

        case "panel-yellow":
          this.playerClick(2)
          break;

        case "panel-blue":
          this.playerClick(3)
          break;
      }
    },

    deepСomparison(arr1,arr2) {
        if (arr1.join("") == arr2.join("")) {
          return true
        }
        return false
    },

    playerClick(clickNum) {
      this.panelClick.currentPanel.push(clickNum)
      this.panelClick.player.push(clickNum)
      this.sounds[clickNum].play()
      setTimeout(() => {
        this.panelClick.currentPanel.pop();
      }, 200);
      console.log("bot: " + this.panelClick.allBot)
      console.log("player: " + this.panelClick.player)
      return this.WinOrLose()
    },

    WinOrLose() { // Как-то сделать проверку на поражение в реальном времени а не по длине массивов
      if (this.panelClick.player.length == this.panelClick.allBot.length) {
        if (this.deepСomparison(this.panelClick.player, this.panelClick.allBot)) {
          this.panelClick.player = [];
          this.panelClick.allBot = [];
          this.score++
          setTimeout(() => {
            return this.gameStart()
          },1500);
        } else {
        alert("lose")
        window.location.reload()
      }
     }
    },

    botClick(panel) {
      return new Promise((resolve, reject) => {
        this.panelClick.currentPanel.push(panel) // проверка на изменение класса
        this.panelClick.allBot.push(panel) // все кликнутые ботом панели
        this.sounds[panel].play()
        setTimeout(() => {
          this.panelClick.currentPanel.pop();
          setTimeout(() => {
            resolve()
          }, 400);
        }, 1500);
      })
    },

    async gameStart() {
      this.clickable = false
      this.$refs.score.innerText = `Score: ${this.score}`
      console.log(this.$refs)
      this.$refs.startBtn.disabled = true
      for(let i = 0; i < this.score; i++) {
        var random = this.randomNum()
        await this.botClick(random)
      }
      this.clickable = true
    },

    randomNum() {
      return this.panelsList[Math.floor(Math.random() * 4)]
    }
  }
}
</script>

<style lang="sass">

  .game
    display: flex
    flex-wrap: wrap
    min-width: 500px
    min-height: 500px
    border-radius: 100%
    box-shadow: 0px 0px 35px black
    justify-content: center
    align-items: center
    overflow: hidden
    background-image: radial-gradient(gray, white 80%)

    .panel-green, .panel-red, .panel-yellow, .panel-blue
      height: 250px
      flex-basis: 50%
      display: block
      height: 250px
      border: none
      cursor: pointer
      position: relative
        
    .panel-green
      background-color: rgba(0, 128, 0, 1)
      right: 7px
      bottom: 7px
    
    .panel-green-click
      background-color: rgba(0, 128, 0, 0.5)
      border-right: 1px solid black
      border-bottom: 1px solid black

    .panel-red
      background-color: rgba(250, 10, 0, 1)
      left: 7px
      bottom: 7px
    
    .panel-red-click
      background-color: rgba(250, 0, 0, 0.5)
      border-left: 1px solid black
      border-bottom: 1px solid black
        
    .panel-yellow
      background-color: rgba(255, 170, 0, 1)
      top: 7px
      right: 7px
      
    .panel-yellow-click
      background-color: rgba(255, 215, 0, 0.5)
      border-top: 1px solid black
      border-right: 1px solid black

    .panel-blue
      background-color: rgba(0, 0, 255, 1)
      top: 7px
      left: 7px
      
    .panel-blue-click
      background-color: rgba(0, 0, 255, 0.5)
      border-top: 1px solid black
      border-left: 1px solid black

    .bar
      width: 220px
      height: 220px
      position: absolute
      background-color: white
      border-radius: 100%
      background-image: radial-gradient(white, gray 90%)
      display: flex
      justify-content: center
      align-items: center
      flex-wrap: wrap
      flex-direction: column

    .bar_score
      font-size: 19px
      position: relative
      bottom: 20px
      font-family: Arial 

    .bar_start
      font-weight: bold
      font-size: 23px
      font-family: Arial
      cursor: pointer
      border: 1px solid #3c3c3c
      border-radius: 10px
      padding: 5px 15px
      background-color: transparent
      transition-duration: 0.5s
      &:active
        background-color: white
        transition-duration: 0s
      &:disabled
        color: black
    
    .bar_diff
      display: flex
      width: 150px
      justify-content: space-around
      position: relative
      top: 7px

    .bar_diff-dot
      position: relative
      cursor: pointer

    .bar_diff-dot::after
      content: ""
      width: 10px
      font-size: 15px
      position: absolute
      top: 18px

    .dot1::after
      content: "Easy"
      right: 15px

    .dot2::after
      content: "Medium"
      right: 24px

    .dot3::after
      content: "Hard"
      right: 13px
</style>

